.container {
  max-width: 1280px;
  padding: 0 20px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

p {
  margin-block-start: 0em;
  margin-block-end: 0em;
}

html {
  min-height: 100%;
}

body {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.modal-window-open {
  overflow: hidden;
}

.header {
  padding-top: 25px;
  padding-bottom: 25px;
  position: relative;
}
.header__flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header-manipulation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: "Raleway", sans-serif;
  font-size: 16px;
  line-height: 18.78px;
  font-weight: 400;
  color: #000000;
}
.header-manipulation > *:not(:last-child) {
  margin-right: 33px;
}
.header-manipulation__number {
  color: #000000;
}
.header-manipulation__basket img {
  width: 30px;
}
.header-manipulation__select {
  font-family: "Raleway", sans-serif;
  font-size: 16px;
  line-height: 18.78px;
  font-weight: 400;
  color: #000000;
  border: none;
}
.header__burger {
  display: none;
  border: none;
  background-color: inherit;
  padding: 10px;
  cursor: pointer;
  transition: all 0.3s;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  -webkit-tap-highlight-color: transparent;
}
.header__burger-img {
  width: 30px;
  height: 30px;
}
.header__burger--open {
  opacity: 0;
}

.navigation__links {
  transition: color 0.4s;
  font-family: "Raleway", sans-serif;
  font-size: 16px;
  line-height: 18.78px;
  font-weight: 400;
  color: #000000;
}
.navigation__links:not(:last-child) {
  margin-right: 30px;
}
.navigation__links:hover {
  color: #DA1F39;
}

.navigation__links--active {
  color: #DA1F39;
}

.logo {
  display: block;
}
.logo__item {
  display: block;
  width: 148px;
}

@media (max-width: 950px) {
  .header-manipulation > *:not(:last-child) {
    margin-right: 10px;
  }
  .header-manipulation__select {
    font-size: 14px;
  }
  .header-manipulation__number {
    font-size: 14px;
  }
  .navigation__links {
    font-size: 14px;
  }
  .navigation__links:not(:last-child) {
    margin-right: 10px;
  }
}
@media (max-width: 700px) {
  .navigation {
    display: none;
  }
  .navigation--mobile {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
  }
  .navigation__links:not(:last-child) {
    margin-right: 0px;
  }
  .navigation__links--mobile {
    font-family: "Dela Gothic One", sans-serif;
    font-size: 18px;
    line-height: 26.06px;
    font-weight: 800;
    color: #9A9A9A;
    margin-bottom: 20px;
    text-align: center;
  }
  .header {
    padding-top: 10px;
    padding-bottom: 10px;
  }
  .header__burger {
    display: block;
  }
  .header-manipulation__number {
    display: none;
  }
  .header-manipulation__select {
    display: none;
  }
}
@media (max-width: 450px) {
  .header__burger {
    padding: 5px;
  }
}
.header-catalog {
  padding-top: 30px;
  padding-bottom: 30px;
  display: flex;
  justify-content: space-between;
  position: absolute;
  width: 100%;
  max-width: 1280px;
  max-height: 310px;
  top: -500%;
  transition: top 0.5s;
}
.header-catalog--active {
  top: 75%;
  z-index: 1000;
}
.header-catalog__left {
  max-width: 150px;
  font-family: "Dela Gothic One", sans-serif;
  font-size: 18px;
  line-height: 26.06px;
  font-weight: 800;
  color: #9A9A9A;
}
.header-catalog__right {
  width: 60%;
  max-height: 310px;
  overflow-y: auto;
}
.header-catalog__img {
  max-width: 200px;
}
.header-catalog-window-list li:not(:last-child) {
  margin-bottom: 25px;
}
.header-catalog-window-list__item {
  text-transform: uppercase;
  color: #9A9A9A;
  transition: color 0.3s;
}
.header-catalog-window-list__item:hover {
  color: #000000;
}
.header-catalog-window-sublist {
  display: flex;
  flex-wrap: wrap;
  justify-content: left;
  padding-left: 20px;
  padding-right: 20px;
  gap: 50px;
}
.header-catalog-window-sublist__item {
  font-family: "Raleway", sans-serif;
  font-size: 14px;
  line-height: 23.27px;
  font-weight: 300;
  color: #000000;
  text-transform: uppercase;
  color: #9A9A9A;
  cursor: pointer;
  transition: all 0.5s;
}
.header-catalog-window-sublist__item:hover {
  transform: scale(1.1);
  color: #DA1F39;
}

@media (max-width: 700px) {
  .header-catalog {
    display: none;
  }
}
.mobile-menu {
  position: absolute;
  top: 100px;
  left: -200%;
  width: 95vw;
  min-height: 30vh;
  box-shadow: 1px 1px 18px -9px;
  border-radius: 10px;
  padding: 30px;
  transition: left 0.5s;
  box-sizing: border-box;
  display: none;
  background-color: #f7f7f7;
}
.mobile-menu input {
  display: none;
}
.mobile-menu input:checked + label {
  transition: all 0.3s;
  font-family: "Raleway", sans-serif;
  font-size: 16px;
  line-height: 18.78px;
  font-weight: 700;
  color: #000000;
}
.mobile-menu__choose-value {
  cursor: pointer;
  font-family: "Raleway", sans-serif;
  font-size: 16px;
  line-height: 18.78px;
  font-weight: 400;
  color: #000000;
}
.mobile-menu__choose-value:not(:last-child) {
  margin-right: 3px;
}
.mobile-menu__choose-flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.mobile-menu__line {
  height: 30px;
  width: 1px;
  border-radius: 1px;
  background-color: black;
}
.mobile-menu--open {
  left: 2%;
}

@media (max-width: 700px) {
  .mobile-menu {
    display: block;
  }
}
@media (max-width: 400px) {
  .mobile-menu {
    padding: 10px;
  }
}
@media (min-width: 500px) {
  .mobile-menu__choose-flex {
    max-width: 60%;
    margin: 0 auto;
  }
}